*{
  font-family: "Inter", sans-serif;
}
body{
  background-color: #EAEAEA
}
.App {
  text-align: center;
}
a {
  text-decoration: none;
  color: inherit;
  width: auto;
  height: auto;
}
.my-button-link {
  border: none;
  border-radius: 32px;
  padding: 0;
  display: flex;
  align-items: center;
  color: white;
  background: #07A4FB;
  font-weight: 800;
  cursor: pointer;
  >img{
      margin-right: 5%;
  }
}
.linkMain{
  margin-left: 2%;
}
.reg-vh, .t{
    margin-bottom: 35px;
    padding: 4.5%;
    padding-left: 1%;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    font-size: 20px;
    align-items: center;
    width: 100%;
}
